	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2005-11-22-social-changes-at-amazon/index.md absPath of file >>> MarkdownRemark","html":"<p>There are some interesting changes happening at Amazon.  <a href=\"http://www.amazon.com/gp/product/B0007VJ7I8/104-9757223-1894316?v=glance&#x26;n=172282&#x26;_encoding=UTF8&#x26;v=glance\">Pick a product and look close</a>. \nYou will see two new features.  One is the addition of tags. \nYou can now add a tag to a product.  As you do you will be\nprompted with tags that others have used.  After you tag it, you\ncan click on the tag and see other products that have the same\ntag.  The second big feature is the addition of a customer\nwiki.  Customers can modify one of the product descriptions. \nVery interesting.</p>","frontmatter":{"title":"Social changes at Amazon","date":"November 22, 2005","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{"path":"/social-changes-at-amazon"}}

